import {
  creatUlElement,
  createCostElement,
  packageInfo,
  scrollNumber,
  setValues,
  priceInfo,
} from "./function.js";
// import { f } from "./checkbox.js";

const section = document.querySelector("section");

export const block = document.createElement("div");
block.classList.add("block");

section.prepend(block);

const blockTitle = document.createElement("h2");
blockTitle.classList.add("block__title");
blockTitle.textContent = "Фасовка";

block.append(blockTitle);

creatUlElement("Ширина", "см.", "width");
creatUlElement("Высота", "см.", "height");
creatUlElement("Толщина", "мкм.", "thickness");
creatUlElement("Количество", "шт.", "circulation");
creatUlElement("Вес", "кг.", "weight");

export const priceMaterials = document.createElement("div");
priceMaterials.classList.add("block__price-materials");

block.append(priceMaterials);

createCostElement("cost-materials", 'cost', "Себест...");
createCostElement("selling-price", 'sell', "Продажа");

packageInfo("Вес пакета", "г.", 'one','ulOne');
packageInfo("Вес партии", "кг.", 'all', 'ulAll');
packageInfo("Кол-во пакетов", "шт.", 'total', 'ulTotal');

priceInfo();

scrollNumber();
setValues();